.admin__container {
  width: 100%;
}

.dashboard__message {
  display: flex;
  justify-content: center;
  border: 2px solid black;
  margin: 0.5rem auto;
  background-color: var(--clr-fourth);
  color: var(--clr-black);
  border-radius: 5px;
  width: 80%;
  box-shadow: var(--bs-basic);
  text-align: center;
}

.dashboard__form {
  border-radius: 5px;
  background-color: var(--clr-white);
  box-shadow: var(--bs-infos);
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
  margin: 0 auto;
  padding: 2rem;
  width: 80%;
}

.dashboard__login,
.dashboard__creation {
  display: flex;
  flex-direction: column;
  width: 80%;
}

.dashboard__label {
  margin: 0.5rem auto;
  text-decoration: underline;
}

.dashboard__input {
  border-radius: 5px;
  border: 2px solid black;
  margin-bottom: 0.5rem;
}

.dashboard__textarea {
  border-radius: 5px;
  border: 2px solid black;
  margin-bottom: 0.5rem;
}

.dashboard__button {
  margin: 1rem auto 0;
  background-color: var(--clr-white);
  color: var(--clr-second);
  border: 2px solid var(--clr-black);
  border-radius: 5px;
  box-shadow: var(--bs-basic);
  transition: all 450ms ease-in;
}

.dashboard__button:hover {
  margin: 1rem auto 0;
  background-color: var(--clr-second);
  color: var(--clr-white);
  border: 2px solid black;
  border-radius: 5px;
}

.dashboard__label--file {
  background-color: var(--clr-second);
  color: var(--clr-white);
  border: 2px solid var(--clr-black);
  margin: 0 auto;
  width: 40%;
  text-align: center;
  padding: 0.5rem;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: var(--bs-basic);
  transition: all 450ms ease-in;
}

.dashboard__label--file:hover {
  background-color: var(--clr-black);
}

.dashboard__filename {
  text-align: center;
  margin-top: 0.8em;
}

@media screen and (min-width: 42em) {
  .dashboard__form,
  .dashboard__message {
    width: 50%;
  }
}

@media screen and (min-width: 62em) {
  .dashboard__form,
  .dashboard__message {
    width: 35%;
  }
}